import React                                        from 'react';
import PropTypes                                    from 'prop-types';
import {
    connect
}                                                   from 'react-redux';
import axios, { post }                              from 'axios';


import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';

import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import TextField                                    from 'material-ui/TextField';
import RaisedButton                                 from 'material-ui/RaisedButton';

import FlatPagination                               from 'material-ui-flat-pagination';







/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionCompanyOnBoardApprove , { ReduxEnumCompanyOnBoardApprove } from '../../../../_redux/action/reduxAction.company.onboard.approve';













/* ################################################### */
/*          helper         */
/* ################################################### */

import { AppConstant }                              from '../../../../_constant/constant.app';
import { ServiceEnumHTTP }                          from '../../../../_service/helper/service.enum.http';
import { StringUtil }                               from '../../../../_util/util.string';

import HelperVerifyAuthorize                        from '../../../_helper/h.verifyAuthorize';
import HelperNavigate , { HelperNavigateENUM }		from '../../../_helper/h.navigate';
import HelperStorage		                        from '../../../_helper/h.storage';







/* ################################################### */
/*          component         */
/* ################################################### */
import ComponentAbstract                            from '../../../_abstract/c.abstract';


import ComponentCustomAlert                         from '../../../_component/alert/cc.alert';
import ComponentCustomLoading                       from '../../../_component/loading/cc.loading';


import ComponentComponyOnboardDetailInfo            from './info/c.onboardApprove.detail.info';
import ComponentCompanyOnboardDetailDocument        from './document/c.onboardApprove.detail.document';






import './s.onboardApprove.detail.css';




export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxCompanyOnboardApprove' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumCompanyOnBoardApprove.INIT_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnBoardApprove.DETAIL_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestDetailDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnBoardApprove.SERVICE_PACKAGE_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestServicePackageDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnBoardApprove.APPROVE_TASK_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestApproveTaskDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnBoardApprove.REJECT_TASK_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestRejectTaskDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnBoardApprove.ERROR == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestError';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];


                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

         class ComponentCompanyOnboardApproveDetail extends ComponentAbstract {



            constructor( props ) {
                super( props )

                this.state = {

                    isError                 : false
                    , errorMsg              : ''


                    , data        : {
                        servicePackage  : []
                    }

                    , form      : {
                        formDoc : {
                            thumbnailDocumentId         : ''
                            , thumbnailUploadDate       : ''
                            , thumbnailUploadBranch     : ''
                            , thumbnailUploadBy         : ''
                            , thumbnailUrl              : ''
                        }
                    }

                    , result    : {

                        info            : {}
                        , document      : []
                        , documentPdf   : []

                    }

                }
            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */

            _onReady() {
                console.log( 'ComponentCompanyOnboardApproveDetail : _onReady : *** ' );

                this.requestDetail( this.props.location.state );
            }












            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestDetail( resultObject ) {
                console.log( 'ComponentCompanyOnboardApproveDetail : requestDetail : *** : resultObject => ' , resultObject );

                let result = this.state.result;
                result[ 'info' ][ 'cifNumber' ]         = resultObject.instructionObject.companyOnboardingInst.cifNumber;
                result[ 'info' ][ 'companyName' ]       = resultObject.instructionObject.companyOnboardingInst.bussName;
                result[ 'info' ][ 'servicePackage' ]    = resultObject.instructionObject.companyOnboardingInst.spCode;
                result[ 'info' ][ 'status' ]            = resultObject.status;
                result[ 'info' ][ 'remark' ]            = '';
                result[ 'info' ][ 'instructionId' ]     = resultObject.instructionId;
                result[ 'info' ][ 'taskId' ]            = resultObject.taskId;
                result[ 'info' ][ 'taskType' ]          = resultObject.taskType;

                result[ 'document' ]                    = resultObject.instructionObject.companyOnboardingInst.documents;
                result[ 'documentPdf' ]                 = [];

                this.setState(
                    {
                        isLoading : true
                        , result : result
                    }
                    , () => {
                        this.requestDocumentSource();
                        this.requestServicePackage();
                    }
                );

            }

            requestDocumentSource() {

                console.log( 'ComponentCompanyOnboardApproveDetail : requestDocumentSource : *** ' );

                if ( this.state.result.document.length == 0 ) {
                    return;
                }

                let tmpList = this.state.result.document;
                let docId = this.state.result.document[ 0 ][ 'documentId' ];
                tmpList.splice( 0 , 1 );



                (
                    new Promise(
                        ( resolve , reject ) => {

                            if ( AppConstant.isDebug() ) {
                                resolve( true );
                                return;
                            }


                            HelperVerifyAuthorize.verify()
                                .then(
                                    () => {

                                        Promise.all( [
                                            ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                            , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                        ] ).then(
                                            ( value ) => {

                                                axios( {
                                                    method      : 'GET'
                                                    , url       : AppConstant.HTTP_SERVER_IP + StringUtil.replace( ServiceEnumHTTP.API.DOCUMENT_ONBOARD_DOWNLOAD , '{documentId}' , docId )
                                                    , headers     : {
                                                        'Authorization' : value[ 0 ] + ' ' + value[ 1 ]
                                                        , 'Content-Type': 'multipart/form-data'
                                                    }
                                                    , responseType : "blob"
                                                } ).then(
                                                    ( responseGet ) => {


                                                        let result = this.state.result;
                                                        result[ 'documentPdf' ].push( {
                                                            documentId : docId
                                                            , links : [
                                                                {
                                                                    rel : ''
                                                                    , href : window.URL.createObjectURL( new Blob( [ responseGet.data ] , { type : 'application/pdf' } ) )
                                                                    , method : ''
                                                                }
                                                            ]
                                                        } );

                                                        this.setState(
                                                            { result : result }
                                                            , () => {
                                                                resolve( true );
                                                            }
                                                        );


                                                    }
                                                )

                                            }
                                        )

                                    }
                                ).catch (
                                    ( error ) => {
                                        this.requestError( error );
                                    }
                                ) ;


                        }
                    )
                ).then(
                    ( result ) => {
                        this.requestDocumentSource();
                    }
                ).catch(
                    ( error ) => { }
                ) ;

                let result = this.state.result;
                result[ 'document' ] = tmpList;
                this.setState( { result : result } );

            }




            requestServicePackage() {
                console.log( 'ComponentCompanyOnboardApproveDetail : requestServicePackage : *** '  );

                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                        Promise.all( [
                                            ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                            , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                        ] ).then(
                                            ( value ) => {

                                                this.dispatch(
                                                    ReduxActionCompanyOnBoardApprove.rServicePackage(
                                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                        , { }
                                                    )
                                                )

                                            }
                                        )

                                    }
                                ).catch (
                                    ( error ) => {
                                        this.requestError( error );
                                    }
                                ) ;
                    }
                );

            }
            requestServicePackageDone( result ) {
                console.log( 'ComponentCompanyOnboardApproveDetail : requestServicePackageDone : ***  : result => ' , result );

                let data = this.state.data;
                data[ 'servicePackage' ] = result;
                this.setState( {
                    isLoading   : false
                    , data      : data
                } );
                this.dispatch( ReduxActionCompanyOnBoardApprove.rRequestEnd( ) );
            }

            requestApproveTask() {
                console.log( 'ComponentCompanyOnboardApproveDetail : requestApproveTask : *** '  );

                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            this.dispatch(
                                                ReduxActionCompanyOnBoardApprove.rApprove(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , {
                                                        taskId          : this.state.result.info.taskId
                                                        , taskType      : this.state.result.info.taskType
                                                        , instructionId : this.state.result.info.instructionId
                                                    }
                                                )
                                            )

                                        }
                                    )
                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;
                    }
                );

            }
            requestApproveTaskDone( result ) {
                console.log( 'ComponentCompanyOnboardApproveDetail : requestApproveTaskDone : ***  : result => ' , result );

                this.setState( {
                    isLoading   : false
                } , () => {
                    HelperNavigate.route(
                        HelperNavigate.mapPath( HelperNavigateENUM.COMPANY_ONBOARD_VERIFY_LIST , {} )
                    );
                } );
                this.dispatch( ReduxActionCompanyOnBoardApprove.rRequestEnd( ) );
            }

            requestRejectTask() {
                console.log( 'ComponentCompanyOnboardApproveDetail : requestRejectTask : *** '  );

                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            this.dispatch(
                                                ReduxActionCompanyOnBoardApprove.rReject(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , {
                                                        taskId          : this.state.result.info.taskId
                                                        , taskType      : this.state.result.info.taskType
                                                        , instructionId : this.state.result.info.instructionId
                                                    }
                                                )
                                            )

                                        }
                                    )
                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;

                    }
                );

            }
            requestRejectTaskDone( result ) {
                console.log( 'ComponentCompanyOnboardApproveDetail : requestRejectTaskDone : ***  : result => ' , result );

                this.setState( {
                    isLoading   : false
                } , () => {
                    HelperNavigate.route(
                        HelperNavigate.mapPath( HelperNavigateENUM.COMPANY_ONBOARD_APPROVE_LIST , {} )
                    );
                } );
                this.dispatch( ReduxActionCompanyOnBoardApprove.rRequestEnd( ) );
            }

            requestError( result ) {
                console.log( 'ComponentCompanyOnboardApproveDetail : requestError : *** : result => ' , result );

                this.setState(
                    { isLoading : false }
                    , () => {

                        this.handlerException(
                            result
                            , () => {
                                this.setState( {
                                    isError         : true
                                    , errorMsg      : result.msg
                                } );
                            }
                        );

                    }
                )

                this.dispatch( ReduxActionCompanyOnBoardApprove.rRequestEnd( ) );

            }













            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */
            render() {

                return (
                        <div className={ ' companyOnboard-approve-detail-height-100p ' }>

                            <div className={ ' companyOnboard-approve-detail-padding-20 ' } >Company Onboard - Onboarding Approval</div>


                            <Grid fluid>
                                <Row className="show-grid">
                                    <Col md={ 6 } >
                                        <ComponentCompanyOnboardDetailDocument
                                                form={ this.state.form.formDoc }

                                                data={ this.state.result.documentPdf }


                                                onClickThumbnail={
                                                    ( docId ) => {

                                                        let form = this.state.form;
                                                        form[ 'formDoc' ][ 'thumbnailDocumentId' ] = '';
                                                        form[ 'formDoc' ][ 'thumbnailUploadDate' ] = '';
                                                        form[ 'formDoc' ][ 'thumbnailUploadBranch' ] = '';
                                                        form[ 'formDoc' ][ 'thumbnailUploadBy' ] = '';
                                                        form[ 'formDoc' ][ 'thumbnailUrl' ] = '';

                                                        for ( let i = 0; i < this.state.result.documentPdf.length; i ++ ) {
                                                            if ( this.state.result.documentPdf[ i ][ 'documentId' ] == docId ) {
                                                                let link = '';
                                                                if ( this.state.result.documentPdf[ i ].links.length > 0 ) {
                                                                    link = this.state.result.documentPdf[ i ].links[ 0 ].href;
                                                                }

                                                                form[ 'formDoc' ][ 'thumbnailDocumentId' ]        = docId;
                                                                form[ 'formDoc' ][ 'thumbnailUploadDate' ]        = '-'
                                                                form[ 'formDoc' ][ 'thumbnailUploadBranch' ]      = '-';
                                                                form[ 'formDoc' ][ 'thumbnailUploadBy' ]          = '-';
                                                                form[ 'formDoc' ][ 'thumbnailUrl' ]               = link;
                                                            }
                                                        }
                                                        this.setState( { form : form } );

                                                    }
                                                }
                                        />
                                    </Col>

                                    <Col md={ 6 } >
                                        <ComponentComponyOnboardDetailInfo
                                            data={ this.state.data }

                                            result={ this.state.result }

                                            onClickApprove={ () => {
                                                this.requestApproveTask();
                                            } }
                                            onClickReject={ () => {
                                                this.requestRejectTask();
                                            } }
                                            onClickCancel={ () => {
                                                HelperNavigate.route(
                                                    HelperNavigate.mapPath( HelperNavigateENUM.COMPANY_ONBOARD_APPROVE_LIST , {} )
                                                );
                                            } }
                                        />
                                    </Col>
                                </Row>
                            </Grid>

                            <ComponentCustomLoading
                                            visible={ this.state.isLoading }
                            />


                            <ComponentCustomAlert
                                visible={ this.state.isError }

                                title={ 'Error' }
                                msg={ this.state.errorMsg }

                                onClose={ () => {
                                    this.setState( {  isError : false } );
                                } }

                                alertType={ 'error' }
                            />

                        </div>
                );
            };
        }

    )



