import React                                        from 'react';
import PropTypes                                    from 'prop-types';
import {
    connect
}                                                   from 'react-redux';



import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';

import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import TextField                                    from 'material-ui/TextField';
import RaisedButton                                 from 'material-ui/RaisedButton';

import FlatPagination                               from 'material-ui-flat-pagination';













/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../../../_abstract/c.abstract';






import './s.onboardApprove.detail.document.css';









export default class ComponentDocUploadDetailDocument extends ComponentAbstract {


    static propTypes = {
        data                    : PropTypes.array
        , form                  : PropTypes.object



        , onClickThumbnail      : PropTypes.func
    }


    static defaultProps = {
        data              : []

        , onClickThumbnail : () => {}
    }







    constructor( props ) {
        super( props )


        this.state = {
            containerWidth : 0
        }
    }











    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
    render() {

        return (
                <div
                        className={ ' companyOnboard-approve-detail-document-container ' }
                        ref={ ( refElement ) => {
                            if ( refElement ) {
                                if ( this.state.containerWidth == 0 && refElement.clientWidth != 0 ) {
                                    this.setState( { containerWidth : refElement.clientWidth } );
                                }
                            }
                        } }>


                    <div className={ ' companyOnboard-approve-detail-document-thumbnail-panel ' } >
                        <div
                                className={ ' companyOnboard-approve-detail-document-thumbnail-container ' }
                                style={ {
                                    width : this.state.containerWidth + 'px'
                                } }>

                            <div
                                    className={ ' companyOnboard-approve-detail-document-thumbnail-container-scroll ' }
                                    style={ { width : ( ( 110 * 7 ) + 20 ) + 'px' } }>
                                {
                                    ( this.props.data ).map( ( value , index ) => {

                                        let link = '';
                                        if ( value.links.length > 0 ) {
                                            link = value.links[ 0 ].href;
                                        }

                                        return (
                                            <div
                                                    key={ '_thumbnail_' + index }
                                                    className={ ' companyOnboard-approve-detail-document-thumbnail ' } >

                                                <div style={ { position : 'relative' , overflow : 'hidden' } }>
                                                    <object
                                                            data={ link }
                                                            type="application/pdf"
                                                            className={ ' companyOnboard-approve-detail-document-image ' }>

                                                    </object>
                                                    <div
                                                            style={ {
                                                                position : 'absolute' , top : '0px' , left : '0px' , width : '90px' , height : '110px'
                                                                , backgroundColor : 'rgba( 4 , 4 , 4 , 0.1 )'
                                                            } }
                                                            onClick={ () => {
                                                                this.props.onClickThumbnail( value.documentId )
                                                            } }> </div>
                                                </div>

                                            </div>
                                        );
                                    } )
                                }
                            </div>
                        </div>

                    </div>


                    <div className={ ' companyOnboard-approve-detail-document-divider ' }></div>


                    <div className={ ' companyOnboard-approve-detail-document-padding-10 ' } >
                        <Grid fluid>
                            <Row className="show-grid">
                                <Col md={ 12 } >
                                    Uploaded Date : { this.props.form.thumbnailUploadDate }
                                </Col>
                            </Row>
                        </Grid>

                        <Grid fluid>
                            <Row className="show-grid">
                                <Col md={ 6 } >
                                    Uploaded Branch : { this.props.form.thumbnailUploadBranch }
                                </Col>
                                <Col md={ 6 } >
                                    uploaded By : { this.props.form.thumbnailUploadBy }
                                </Col>
                            </Row>
                        </Grid>
                    </div>

                    <div className={ ' companyOnboard-approve-detail-document-padding-10 companyOnboard-approve-detail-document-height-70p companyOnboard-approve-detail-document-height-max-700 companyOnboard-approve-detail-document-border ' }>

                        {
                            this.props.form.thumbnailDocumentId == ''
                            ?
                                ( null )
                            :
                                ( () => {

                                    return (
                                        <embed
                                                src={ this.props.form.thumbnailUrl }
                                                className={ ' companyDocUpload-document-image ' }
                                                style={ { width : '100%' , height : '100%' } }
                                        />
                                    );

                                } )( )
                        }

                    </div>

                </div>
        );
    };
}



