import React                                        from 'react';
import PropTypes                                    from 'prop-types';
import {
    connect
}                                                   from 'react-redux';



import {
    Grid , Row , Col
    , Table
}                                                   from 'react-bootstrap';

import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import TextField                                    from 'material-ui/TextField';
import RaisedButton                                 from 'material-ui/RaisedButton';

import FlatPagination                               from 'material-ui-flat-pagination';













/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../../../_abstract/c.abstract';






import './s.onboardApprove.detail.info.css';









export default class ComponentComponyOnboardDetailInfo extends ComponentAbstract {


    static propTypes = {
        data                : PropTypes.object

        , result            : PropTypes.object


        , onClickApprove     : PropTypes.func
        , onClickReject      : PropTypes.func
        , onClickCancel      : PropTypes.func
    }


    static defaultProps = {
        data                    : {}

        , result                : {}

        , onClickApprove        : () => {}
        , onClickReject         : () => {}
        , onClickCancel         : () => {}
    }







    constructor( props ) {
        super( props )
    }










    /*
        ################################################################################################################################################
        ################################################################################################################################################
                event
        ################################################################################################################################################
        ################################################################################################################################################
    */
    onClickDetail() {
        console.log( 'ComponentDocUploadDetail : onClickDetail : *** ' );


    }












    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
    render() {

        return (
                <div>

                    <div className={ ' companyOnboard-approve-detail-info-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    CIF Number
                                </Col>

                                <Col md={ 6 }>
                                    { this.props.result.info[ 'cifNumber' ] }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-approve-detail-info-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    Company Name
                                </Col>

                                <Col md={ 6 }>
                                    { this.props.result.info[ 'companyName' ] }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-approve-detail-info-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    Select Service Package
                                </Col>

                                <Col md={ 6 }>
                                    {
                                        ( () => {
                                            let label = '';
                                            for ( let i = 0; i < this.props.data[ 'servicePackage' ].length; i ++ ) {
                                                let ObjJ = this.props.data[ 'servicePackage' ][ i ];
                                                if ( ObjJ[ 'spCode' ] == this.props.result.info[ 'servicePackage' ] ) {
                                                    label = ObjJ[ 'spName' ];
                                                }
                                            }
                                            return label;
                                        } )()
                                    }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-approve-detail-info-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    Status
                                </Col>

                                <Col md={ 6 }>
                                    { this.props.result.info[ 'status' ] }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-approve-detail-info-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    Reason
                                </Col>

                                <Col md={ 6 }>
                                    { this.props.result.info[ 'remark' ] }
                                </Col>
                            </Row>
                        </Grid>

                    </div>



                    <div className={ ' companyOnboard-approve-detail-info-textAlign-center ' }>

                        <RaisedButton
                            label={ 'Approve Task' }
                            className={ ' companyOnboard-approve-detail-info-margin-20 ' }
                            onClick={ () => {
                                this.props.onClickApprove();
                            } } />

                        <RaisedButton
                            label={ 'Reject Task' }
                            className={ ' companyOnboard-approve-detail-info-margin-20 ' }
                            onClick={ () => {
                                this.props.onClickReject();
                            } } />

                        <RaisedButton
                            label={ 'Cancel' }
                            className={ ' companyOnboard-approve-detail-info-margin-20 ' }
                            onClick={ () => {
                                this.props.onClickCancel();
                            } } />

                    </div>

                </div>
        );
    };
}



